<template>
    <div class="classify-topic w">
        <!-- Tab  -->
        <div class="sort_wrap">
            <ul class="sort_list">
                <li
                    v-for="item in dts"
                    :key="item.id"
                    @click="change(item.id)"
                >
                    <a :class="{ active: item.id == topicID }">{{
                        item.title
                    }}</a>
                </li>
            </ul>
        </div>
        <!--  -->
        <el-row :gutter="15">
            <VideoInfo
                v-for="item in topicVideo"
                :key="item.id"
                :span="6"
                :id="item.id"
                :title="item.title"
                :pic="item.thumb"
                :longUrl="item.video_url"
                :shortUrl="item.short_video_url"
                :view="item.view_num"
                :like="item.like_num"
                :tags="item.tags"
                :time="item.mtime"
                :category="item.category_name"
                :topic_info="item.topic_info"
                :is_like="item.is_like"
            >
            </VideoInfo>
        </el-row>
        <!--  ElementUi -->
        <div class="pager-box">
                <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pagenum"
                :page-sizes="[1, 2, 3, 5]"
                :page-size="pagesize"
                :total="total"
                layout="total, sizes, prev, pager, next"
                prev-text=""
                next-text=""
                background
                style="text-align: center;"
                :hide-on-single-page="true">
            </el-pagination>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            dts: [], // Tab 
            topicID: 1, // idtab  
            topicVideo: [],
            // 
            pagenum: 1, // 
            pagesize: 1, // 
            total: 70, // 
        };
    },
    methods: {
        // Tab 
        change(id) {
            this.topicID = id
            this.pagenum = 1 // Tab,1
        },
        //  axios 
        getCategory () {
            this.$http.video.topic().then(({ data }) => {
                this.dts = data.list
                this.topicID = data.list[0].id //  id  topicID
            })
        },
        //  axios   
        // getTopicVideo (mid) {
        //     this.$http.video.list({
        //         page: this.pagenum, // 
        //         limit: this.pagesize, // 
        //         topic_id: mid
        //     }).then(({ data }) => {
        //         this.topicVideo = data.list;
        //         this.total = data.total // 
        //     }).catch(err => {
        //         console.log(err);
        //     })
        // },
        getTopicVideo(mid) {
            axios.all([
                this.$http.video.list({
                    page: this.pagenum, // 
                    limit: this.pagesize, // 
                    topic_id: mid
                }),
                this.$http.video.videoStar({
                    page: this.pagenum, // 
                    limit: this.pagesize, // 
                    topic_id: mid
                }),
            ]).then(axios.spread((video, videoStar)=> {
                if(video.code && videoStar.code === 200) {
                    this.topicVideo = video.data.list.concat(videoStar.data.list)
                    this.total = video.data.total + videoStar.data.total
                } else if(!video.data) {
                    return
                }
            })).catch(err => {
                console.log(err);
            })
        },
        //--------------------------------------
        // pageSize 
        handleSizeChange(newSize) {
            // console.log(` ${newSize} `);
            this.pagesize = newSize
            this.getTopicVideo(this.topicID)
        },
        // currentPage 
        handleCurrentChange(newPage) {
            // console.log(`: ${newPage}`);
            this.pagenum = newPage
            this.getTopicVideo(this.topicID)
        },
    },
    mounted() {
        this.getCategory()
    },
    //  topicID ,
    watch: {
        //  topicID 
        topicID: {
            // handler , topicID , handler 
            handler(newID) {
                this.topicID = newID
                // console.log("id", newID);
                this.getTopicVideo(newID);
            },
            // immediate: true, // 
        },
    },
};
</script>

<style lang="less" scoped>
.sort_wrap {
    width: 100%;
    height: 40px;
    margin: 15px 0 25px 0;
    text-align: center;
    .sort_list li {
        display: inline-block;
        a {
            display: inline-block;
            margin-right: 10px;
            padding: 0 20px;
            height: 40px;
            border-radius: 2px;
            background-color: #34425a;
            color: #99a9bf;
            line-height: 40px;
            box-shadow: 0 10px 13px rgba(0, 0, 0, 0.1);
            cursor: pointer;
        }
    }
}
// 
.pager-box /deep/ .el-pagination {
    padding: 20px 0;
    .el-pagination__total {
        padding: 0 14px;
        min-width: 35.5px;
        color: #99a9bf;
        border: 1px solid #34425a;
    }
    .el-pagination__total, .btn-prev, .btn-next, .number, .el-input--mini, .el-input__inner,.el-icon {
        height: 39px;
        line-height: 39px;
        border-radius: 3px !important;
        font-size: 14px;
        font-weight: 700;
        // background: #34425a !important;
        box-shadow: 0 3px 1px -2px rgba(0,0,0,.2);
    }
    .el-input--mini .el-input__inner {
        font-size: 15px;
        background-color: rgba(0, 0, 0, 0);
        color: #99a9bf;
        border: 1px solid #34425a;
    }
    button span {
        font-size: 16px !important;
        width: 68px;
        height: 39px;
        line-height: 39px;
        font-weight: 700;
    }
    .number {
        width: 39px;
    }
    .number:hover, .el-icon:hover {
        // background-color: #fb7299 !important;
        color: #fff !important;
        transition: all .5s;
    }
}
// 
.pager-box /deep/ .el-pagination.is-background .el-pager li:not(.disabled).active {
  background-color: #fb7299 !important;
}
.pager-box /deep/ .el-pagination.is-background .el-pager li {
    margin: 0 5px;
    background-color: #34425a;
    color: #99a9bf;
}
// 
.pager-box /deep/ .el-pagination.is-background .btn-prev {
    background-color: #34425a;
    color: #fff;
}
.pager-box /deep/ .el-pagination.is-background .btn-next {
    background-color: #34425a;
    color: #fff;
}
.pager-box /deep/ .el-pagination.is-background .btn-prev:disabled {
    background-color: #34425a;
    color:#99a9bf;
    opacity: 0.5;
}
.pager-box /deep/ .el-pagination.is-background .btn-next:disabled {
    background-color: #34425a;
    color:#99a9bf;
    opacity: 0.5;
}
// 
.active {
    color: #fff !important;
    background-color: #fb7299 !important;
}
</style>