<template>
    <div class="mnlist w">
        <!-- Tab  -->
        <div class="sort_wrap">
            <ul class="sort_list">
                <li v-for="(item, index) in dts" :key="index" @click="change(index)">
                    <a :class="{ active: index == current }">{{ item.name }}</a>
                </li>
            </ul>
        </div>
        <!--  -->
        <MnLib :mnData="mnData"></MnLib>
        <!--  ElementUi -->
        <!-- <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="pagenum"
            :page-sizes="[30, 42, 60, 90]"
            :page-size="pagesize"
            :total="total"
            layout="total, sizes, prev, pager, next, jumper"
            hide-on-single-page= "true"
            background>
        </el-pagination> -->
    </div>
</template>

<script>
// 
import MnLib from "@/components/Meinv/MnLib.vue"
// axios
// import request from "@/utils/request.js"

export default {
    // 
    components: {
        MnLib
    },
    data() {
        return {
            // tab  ()
            current: 0,
            // Tab 
            dts: [],
            mnData: [], // 
            // 
            // userList: [],
            // pagenum: 1, // 
            // pagesize: 30, // 
            // total: 70, // 
        }
    },
    methods: {
        // Tab 
        change(index) {
            this.current = index
            //   
            sessionStorage.setItem("mnListTab", this.current)
        },
        //  axios   
        // async getUserList(mnListUrl) {
        //     const {data: res} = await request.get(mnListUrl)
        //     this.mnData = res.mnList
        // },
        
        //--------------------------------------
        // 
        // async getUserList() {
        //     const {data: res} = await request.get("/articles", {
        //         params: {_page: this.pagenum, _limit: this.pagesize}
        //     })
        //     this.userList = res
        // },
        // // pageSize 
        // handleSizeChange(newSize) {
        //     // console.log(` ${newSize} `);
        //     this.pagesize = newSize
        //     this.getUserList()
            
        // },
        // // currentPage 
        // handleCurrentChange(newPage) {
        //     // console.log(`: ${newPage}`);
        //     this.pagenum = newPage
        //     this.getUserList()
        // },
    },
    created() {
        // 
        this.$http.video.categoryStar().then(({data})=> {
            this.dts = data
        })
    },
    mounted() {
        //    current
        this.current = sessionStorage.getItem("mnListTab")
        // 
        if (this.current == null) {
            this.current = 0
        }
    },
    //  current ,
    watch: {
        //  current 
        current: {
            // handler , current , handler 
            handler(newVal) {
                // 
                this.$http.video.categoryStar().then(({data})=> {
                    this.mnData = data[newVal].children
                })
            },
            immediate: true // 
        },
    },
}
</script>

<style lang="less" scoped>
.sort_wrap {
    width: 100%;
    height: 40px;
    margin: 15px 0;
    .sort_list li {
        display: inline-block;
        a {
            display: inline-block;
            margin-right: 10px;
            padding: 0 20px;
            height: 40px;
            border-radius: 2px;
            background-color: #34425a;
            color: #99a9bf;
            line-height: 40px;
            box-shadow: 0 10px 13px rgba(0,0,0,0.1);
            cursor: pointer;
        }
    }
}
// 
.active {
    color: #fff !important;
    background-color: #fb7299 !important;
}
</style>