//  axios
import axios from '@/http/http.js'

const userApi = {
    // 
    login(data) {
        return axios.post('/login', data)
    },
    // 
    register(data) {
        return axios.post('/register', data)
    },
    // 
    logout() {
        return axios.post('/logout')
    },
    // 
    userInfo() {
        return axios.post("/user")
    },
    // 
    like_on(data) {
        return axios.post("/like/on", data)
    },
    // 
    like_off(data) {
        return axios.post("/like/off", data)
    },
    // 
    viewVideo() {
        return axios.post('user/decShareNum')
    },
    // 
    modification(data) {
        return axios.post("/forget", data)
    },
    // 
    buy_vip(data) {
        return axios.post("/pay/vip", data)
    },
    // 
    userHistoryAdd(data) {
        return axios.post("/video/historyAdd", data)
    },
    // 
    userHistory(data) {
        return axios.post("/video/historyList", data)
    },
    // ()
    userLikeList(data) {
        return axios.post("/like/list", data)
    },
    // 
    userCollect(data) {
        return axios.post("/collect/list", data)
    },
    // ()
    userLike(data) {
        return axios.post("/like/me", data)
    },
}

export default userApi