<template>
    <div class="search-container w">
        <!--  -->
        <div class="search">
            <!--  ElementUI  Select  -->
            <el-select v-model="value"  :popper-append-to-body="false">
                <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
            </el-select>
            <!--  -->
            <button
                class="el-icon-search"
                @click="searchData()">
            </button>
            <!--  -->
            <input type="text" placeholder="" 
                v-model="inputValue"
                @focus="focus_on($event)" 
                @blur="focus_off($event)" 
                @keydown.enter="searchData()" 
            />
        </div>
        <!--  -->
        <transition name="el-zoom-in-center">
        <el-alert 
            v-if="search_no" 
            :title="remind" 
            :type="alertType" 
            center 
            show-icon
            close-text="">
        </el-alert>
        </transition>
        <!--  -->
        <el-row :gutter="15" v-if="searchResultArr.length > 0 && value == '1'">
            <VideoInfo
                v-for="item in searchResultArr"
                :key="item.id"
                :span="6"
                :id="item.id"
                :title="item.title"
                :pic="item.thumb"
                :longUrl="item.video_url"
                :shortUrl="item.short_video_url"
                :view="item.view_num"
                :like="item.like_num"
                :tags="item.tags"
                :time="item.mtime"
                :category="item.category_name"
                :topic_info="item.topic_info"
                :is_like="item.is_like"
            >
            </VideoInfo>
        </el-row>
        <!--  MnLib , , -->
        <MnLib :mnData="searchResultArr" v-if="searchResultArr.length > 0  && value == '2'"></MnLib>
    </div>
</template>

<script>
// 
import MnLib from "@/components/Meinv/MnLib.vue"
// 
// import { throttle } from "@/utils/throttle.js";
import axios from 'axios'

export default {
    components: {
        MnLib
    },
    data() {
        return {
            inputValue:"", // ()
            // 
            options: [{value: '1',label: ''}, {value: '2',label: ''}],
            value: "1",
            // 
            search_no: false,
            remind: "",
            alertType: "info",
            // 
            searchResultArr: [],
        };
    },
    methods: {
        //   keyword:
        getVideoArr(val) {
            axios.all([
                // 
                this.$http.video.list({ keyword: val }),
                this.$http.video.videoStar({ keyword: val })
            ]).then(axios.spread((video, videoStar)=> {
                // ,c = a.concat(b)
                this.searchResultArr = video.data.list.concat(videoStar.data.list)
            }))
        },
        //   
        getStarList(val) {
            this.$http.video.categoryStar().then(({data})=> {
                let starArr = [];
                for(var i = 0; i < data.length; i++) {
                    // 
                    starArr = starArr.concat(data[i].children)
                }
                this.searchResultArr = starArr.filter((item)=> {
                    // 
                    if(item.name.includes(val)) {
                        return item
                    }
                })
            })
        },
        searchData() {
            // 
            if(this.inputValue.trim()) {
                switch (this.value) {
                    case "1":
                        this.getVideoArr(this.inputValue.trim());
                        break;
                    case "2":
                        this.getStarList(this.inputValue.trim())
                        break;
                }
            } else {
                this.searchResultArr = []
                this.search_no = true
                this.alertType = "error"
                this.remind = ""
            }
        },
        // 
        focus_on(e) {
            e.target.parentNode.style.backgroundColor = "#fff"
            e.target.style.color = "#000"
        },
        // 
        focus_off(e) {
            e.target.parentNode.style.backgroundColor = "rgba(255, 255, 255, 0.15)"
            e.target.style.color = "#fff"
        },
    },
    watch: {
        value: {
            handler(newVal) {
                this.value = newVal
                this.search_no = false;
                this.inputValue = "";
                this.searchResultArr = [];
            },
            // immediate: true // 
        },
        searchResultArr: {
            handler(newArr) {
                if(newArr.length <= 0 && this.inputValue.trim()) {
                    this.search_no = true
                    this.alertType = "info"
                    this.remind = ","
                } else if(newArr.length > 0) {
                    this.search_no = false
                }
            }
        }
    },
};
</script>

<style lang="less" scoped>
.search-container {
    /deep/ .el-input__inner{
        width: 80px;
        height: 40px;
        border-radius: 0;
        background-color: rgba(0, 0, 0, 0);
        border: 0;
        border-right: 1px solid #99a9bf;
        color: #99a9bf;
        line-height: 40px;
        font-family: inherit;
    }
    .search {
        position: relative;
        width: 400px;
        height: 40px;
        margin: 0 auto;
        margin-bottom: 20px;
        line-height: 40px;
        border: 1px solid #fff;
        border-radius: 40px;
        background-color: rgba(rgba(255, 255, 255, 0.15));
        box-shadow: 0 13px 15px rgba(0, 0, 0, 0.15);
        transition: all .5s;
        input {
            width: 285px;
            height: 100%;
            padding-left: 10px;
            color: #fff;
            font-size: 16px;
            font-weight: 700;
            outline: none;
            border: 0;
            background-color: rgba(0, 0, 0, 0);
            font-family: inherit;
        }
        button {
            position: absolute;
            right: 3px;
            top: 50%;
            transform: translate(0, -50%);
            width: 34px;
            height: 34px;
            outline: none;
            border: 0;
            border-radius: 34px;
            background-color: #20a0ff;
            box-shadow: 0 3px 4px rgba(32,160,255,.5);
            font-size: 16px;
            font-weight: bold;
            color: #fff;
            cursor: pointer;
        }
    }
    /deep/ .el-alert {
        width: 600px;
        margin: 0 auto;
    }
}
</style>