<template>
    <div class="header-container">
        <!--  -->
        <Spring></Spring>
        <!--  -->
        <transition name = "fade">
        <div class="cloud" v-show = "flag">
            <div class="cloudcon w">
                <!-- logo -->
                <div class="logo"></div>
                <!--  -->
                <div class="add">
                    <img src="../assets/font.png" alt="" />
                </div>
                <!--  -->
                <div class="buy-vip">
                    <!-- <a @click="$router.push('/login')" class="openVip">VIP</a> -->
                    <!-- ElementUi  Dialog -->
                    <div @click="to_login = true" class="menber_r">
                        <el-button type="text" class="btn_login">
                            <img v-if="user" :src="user.avatar ? user.avatar : userIcon" />
                            <img v-else src="../assets/user.png" alt="">
                        </el-button>
                        <span class="text_login">
                            {{ user ? user.nickname || user.username: "/" }}
                        </span>
                    </div>
                    <el-dialog
                        :destroy-on-close = "true"
                        :visible.sync="to_login"
                        width="1250px"
                        center>
                        <User v-if="user"></User>
                        <!--  -->
                        <Login v-else @numchange="getNewCount"></Login>
                    </el-dialog>
                </div>
            </div>
        </div>
        </transition>
        <!-- TabBar  -->
        <div class="tabbar">
            <div class="secondaryHeader w">
                <!--  -->
                <ul class="menu-wrapp">
                    <li v-for="(item, index) in menucat" :key="item.id">
                        <!-- indexcurrent,active -->
                        <a
                            :href="item.path"
                            @click="addClass(index)" 
                            :class="{ active: index==current}">
                            {{ item.title }}
                        </a>
                    </li>
                </ul>
                <!-- ElementUi  Dialog -->
                <el-button type="text" @click="centerDialogVisible = true" class="el-icon-search"></el-button>
                <!-- destroy-on-close  -->
                <el-dialog
                    :destroy-on-close = "true"
                    :visible.sync="centerDialogVisible"
                    width="1250px"
                    center>
                    <Search></Search>
                </el-dialog>
            </div>
        </div>
    </div>
</template>

<script>
// mapStatestate
import { mapState } from 'vuex'
import Information from "@/components/User/user-information.vue"
import Login from '@/views/Login.vue'
import Spring from '@/components/Spring.vue'

export default {
    name: "Header",
    components: {
        User: Information,
        Login,
        Spring,
    },
    data() {
        return {
            centerDialogVisible: false, // 
            to_login: false, // 
            scroll: '',
            // 
            flag: true,
            // ,
            current: 0,
            // 
            menucat: [
                // a #
                { title: "", path: "#/" },
                { title: "", path: "#/meinv" },
                { title: "", path: "#/category" },
                { title: "", path: "#/new" },
                { title: "", path: "#/tuijian" },
                { title: "", path: "#/hot" },
                { title: "", path: "#/free" },
                { title: "", path: "#/photo" },
            ],
            userIcon: "https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png",
        }
    },
    mounted() {
        //  
        window.addEventListener('scroll', this.menu)
        //    current
        this.current = sessionStorage.getItem("current")
        // 
        if (this.current == null) {
            this.current = 0
        }
    },
    methods: {
        menu() {
            //  
            this.scroll = document.documentElement.scrollTop || document.body.scrollTop;
            if (this.scroll >= 300) {
                this.flag = false
            } else {
                this.flag = true
            }
        },
        // ,()
        addClass(index) {
            // index 
            this.current = index
            //   
            sessionStorage.setItem("current", this.current)
            //     
            // console.log(this.$route.path);
        },
        // 
		getNewCount(val) {
            // 
            this.to_login = val
		},
    },
    computed: {
        // mapState  $store.state.user
        // user  store/index.js  state 
        ...mapState(['user'])
    },
};
</script>

<style lang="less" scoped>
// ,
.active {
    background-color: #d54d3f;
}
// 
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
// Header 
.header-container {
    position: fixed;
    width: 100%;
    height: auto;
    background-color: #28374a;
    z-index: 9999;
    // 
    .cloud {
        height: 80px;
        margin-bottom: 5px;
        .cloudcon {
            height: 100%;
            box-shadow:0px 12px 8px -12px #000;
            .logo {
                display: inline-block;
                width: 260px;
                height: 100%;
                padding-left: 50px;
                background: url("../assets/logo.png") no-repeat;
            }
            .add {
                display: inline-block;
            }
            .buy-vip {
                float: right;
                width: 304px;
                height: 100%;
                .menber_r {
                    width: 160px;
                    height: 40px;
                    float: right;
                    margin-top: 22px;
                    .btn_login {
                        float: left;
                        width: 40px;
                        height: 40px;
                        border: 2px solid #ccc;
                        margin: 0;
                        overflow: hidden;
                        img {
                            width: 40px;
                            height: 40px;
                            object-fit: cover;
                        }
                    }
                    .text_login {
                        display: inline-block;
                        width: 110px;
                        height: 40px;
                        margin-left: 10px;
                        line-height: 40px;
                        color: #f3f3f3;
                        text-shadow: 0px 0px 6px rgba(0, 0, 0, 0.6);
                        cursor: pointer;
                        overflow: hidden;
                        text-overflow:ellipsis;
                        white-space: nowrap;
                    }
                }
            }
        }
    }
    // TabBar 
    .tabbar {
        width: 100%;
        height: 50px;
        background-color: #28374a;
        box-shadow: 0 2px 3px rgba(0,0,0,.3);
        .secondaryHeader {
            display: flex;
            height: 100%;
            overflow: hidden;
            .menu-wrapp {
                flex: 9;
                li, a {
                    display: inline-block;
                }
                li {
                    border-right: 1px solid #34425a;
                    a {
                        height: 50px;
                        color: #f3f3f3;
                        font-size: 18px;
                        line-height: 50px;
                        padding: 0 25px;
                        text-shadow: 0 1px 1px rgba(0,0,0,.2);
                    }
                    a:hover {
                        background-color: #34425a;
                    }
                }
            }
            .menu-wrapp li:last-child {
                    border-right: 0;
                }
        }
    }
    // 
    /deep/.el-dialog, .el-pager li {
        background: #28374a;
    }
    /deep/ .el-button {
        width: 32px;
        height: 32px;
        background-color: #445573;
        border-radius: 32px;
        box-shadow: 0 10px 13px rgba(0,0,0,.1);
        color: #fff;
        font-size: 16px;
        font-weight: bold;
        padding: 0;
        margin-top: 9px;
    }
    /deep/ .el-dialog__headerbtn {
        width: 30px;
        height: 30px;
        border-radius: 30px;
        background-color: #99a9bf;
    }
    /deep/ .el-dialog__headerbtn .el-dialog__close {
        color: #fff;
    }
    /deep/ .el-dialog__wrapper {
        backdrop-filter: blur(2px) brightness(50%);
        -moz-backdrop-filter: blur(2px) brightness(50%);
        -webkit-backdrop-filter: blur(2px) brightness(50%);
    }
    /deep/ .el-dialog__header,.el-dialog__body {
        padding: 0;
    }
}
</style>