<template>
    <div class="hot_new-container">
        <!--  -->
        <span class="hot-icon"></span>
        <!--  -->
        <ul class="hot-list">
            <li class="r2">
                <!-- 1. , -->
                <a v-for="item in hotList" :key="item.id" @click="toPath(item.id, item.title, item.video_url)">
                    <img :src="item.thumb" alt="">
                    <span class="bt">{{ item.title }}</span>
                </a>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    props: ["hotList"],
    name: "HotNew",
    methods: {
        // 2. ,,
        toPath(id, tit, url) {
            const { href } = this.$router.resolve({
                name: "myPlaye",
                query: { id: id },
            });
            // local
            localStorage.setItem('rowData', JSON.stringify({
                src: url, title: tit
            }))
            window.open(href, '_blank');
            // this.$router.push({
            //     // params   name 
            //     name: "myPlaye",
            //     //  get ,
            //     query: { id: id},
            //     //   params  post,
            //     params: { src: url, title: tit }
            // })
        }
    }
};
</script>

<style lang="less" scoped>
.hot_new-container {
    position: relative;
    width: 100%;
    height: 100%;
    .hot-icon {
        position: absolute;
        right: -8px;
        top: -8px;
        width: 80px;
        height: 80px;
        background: url("../../assets/hot.png") no-repeat;
        background-size: 80px;
        z-index: 99;
    }
    .hot-list {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 206px;
        height: 389px;
        .r2 a {
            position: relative;
            display: block;
            width: 205px;
            height: 118px;
            margin-bottom: 15px;
            border: 1px solid #28374a;
            cursor: pointer;
            overflow: hidden;
            border-radius: 7px;
            img {
                position: absolute;
                width: 100%;
                height: 100%;
                // 
                object-fit: cover;
                transition:all 0.5s;
                filter: brightness(75%); // 
            }
            .bt {
                position: absolute;
                bottom: 0px;
                width: 100%;
                height: 22px;
                background-color: rgba(0, 0, 0,.5);
                color: #fff;
                text-shadow: 0 3px 5px rgba(0,0,0,.3);
                font-size: 12px;
                text-align: center;
                line-height: 22px;
                overflow: hidden;
            }
        }
        .r2 a:hover img {
            // 1.2
            filter: brightness(100%); // 
            transform:scale(1.2);
        }
    }
}
</style>