<template>
    <div class="home-container w">
        <div class="home-slide">
            <!--  -->
            <div class="to_login">
                <UserInfo></UserInfo>
            </div>
            <!--  -->
            <div class="merry-go-round">
                <Swiper :swiperList="swiperMovies"></Swiper>
            </div>
            <!--  -->
            <div class="hot_news">
                <HotNew :hotList="hotMovies"></HotNew>
            </div>
        </div>
        <!-- Tab  -->
        <TabBox></TabBox>
        <!--  -->
        <RandomVideo></RandomVideo>
        <!--  -->
        <Topic></Topic>
    </div>
</template>

<script>
// 
import UserInfo from "@/components/Home/UserInfo.vue"
import Swiper from "@/components/Home/Swiper.vue"
import HotNew from "@/components/Home/HotNew.vue"
import TabBox from "@/components/Home/TabBox.vue"
import RandomVideo from "@/components/Home/RandomVideo.vue" // 
import Topic from "@/components/Home/Topic.vue" // 
//  axios
import axios from "axios"

export default {
    name: "Home",
    // 
    components: {
        UserInfo,
        Swiper,
        HotNew,
        TabBox,
        RandomVideo,
        Topic
    },
    data() {
        return {
            // 
            swiperMovies: [],
            // 
            hotMovies: [],
        }
    },
    methods: {
        // 
        getData () {
            axios.all([
                // apiswiperVideo  api/video.js 
                this.$http.video.swiperVideo(), // 
                this.$http.video.hotList(), // 
            ]).then(axios.spread((advert, hotVideo) => {
                this.swiperMovies = advert.data // 
                this.hotMovies = hotVideo.data.list // 
            })).catch(err => {
                console.log("", err);
            })
        },
    },
    mounted() {
        this.getData()
    }
};
</script>

<style lang="less" scoped>
.home-container {
    padding-top: 135px; // 
    .home-slide {
        position: relative;
        width: 100%;
        height: 410px;
        // border: 1px solid red;
        margin-top: 20px;
        // 
        .to_login, .hot_news {
            position: absolute;
            width: 222px;
            height: 100%;
            // background-color: #28374a;
            // background: -webkit-linear-gradient(bottom,#28374a,#34425a 15px) #28374a;
            // border: 1px solid #28374a;
            box-shadow: 0 10px 13px rgba(0,0,0,0.1);
        }
        // 
        .merry-go-round {
            position: absolute;
            left: 50%;
            transform: translate(-50%);
            width: 710px;
            height: 100%;
            border-radius: 7px;
            overflow: hidden;
            box-shadow: 0 10px 13px rgba(0,0,0,0.1);
        }
        // 
        .hot_news {
            background-color: #34425a;
            right: 0;
        }
    }
    
}
</style>