<template>
    <div class="new_top">
        <span class="icon-top el-icon-s-data">&nbsp;</span>
        <ul>
            <li v-for="(item, index) in hotMnData" :key="index">
                <span class="num" :style="{backgroundPositionY: y[index] }"></span>
                <span class="top_pic">
                    <i class="icon" 
                        v-if="item.icon"
                        :style="{backgroundImage: 'url('+item.icon+')'}">
                    </i>
                    <i v-else class="icon"></i>
                </span>
                <div class="top_info">
                    <p>{{ item.name }}</p>
                    <!-- <i class="el-icon-star-on">{{ item.stars }}</i> -->
                </div>
                <!-- 1. , -->
                <a @click="toPath(item.id, item.name, item.icon, item.summary, item.works)" class="btn"></a>
            </li>
        </ul>
    </div>
</template>

<script>
// (1) axios
// import request from "@/utils/request.js"

export default {
    name: "NewsLeft",
    data() {
        return {
            hotMnData: [], // 
            //  icon 
            y: ["18px", "-39px", "-99px", "-157px", "-214px", "-270px", "-330px", "-386px", "-445px", "-503px"],
        }
    },
    methods: {
        // (2) 
        getUserList() {
            this.$http.video.categoryStar({ pid: 2 }).then(({data})=> {
                this.hotMnData = data.slice(0,10)
            })
        },
        // 2. ,,
        toPath(id, name, pic, summary, works) {
            this.$router.push({
                path: "/lib/" + id,
                // query get ,
                query: { title: name},
                // params  post,
                // params: { pic: pic, summary: summary, works: works},
            })
            // local
            localStorage.setItem('mnInfo', JSON.stringify({
                id: id, pic: pic, summary: summary, works: works
            }))
        }
    },
    created() {
        this.getUserList()
    }

};
</script>

<style lang="less" scoped>
// 
.new_top {
    position: relative;
    float: right;
    margin: 20px auto;
    height: 635px;
    width: 238px;
    border: 1px dotted #99A9BF;
    // background-color: #fff;
    background-color: #34425a; // 
    /* text-align: center; */
}
.icon-top {
    display: inline-block;
    width: 100%;
    height: 35px;
    // background-color: #c0392b;
    // text-align: center;
    margin-left: 5px;
    line-height: 35px;
    color: #f6f5ec;
    overflow: hidden;
}
.new_top ul {
    position: absolute;
    left: 50%;
    bottom: 5px;
    transform: translate(-50%);
    display: flex;
    /* Y */
    flex-direction: column;
    /*  */
    justify-content: space-between;
    height: 600px;
    width: 228px;
}
.new_top ul li {
    position: relative;
    display: flex;
    flex: 1;
    list-style: none;
    margin-top: 10px;
    // border: 1px double #F66;
    border: #99A9BF dashed 1px;
    overflow: hidden;
    // background-color: #fff;
    box-shadow: 0 8px 6px rgba(0,0,0,.05);
}
.new_top ul li .num {
    flex: 1;
    /* background-color: orange; */
}
.new_top ul li .top_pic {
    position: relative;
    flex: 2;
    // background-color: orangered;
}
.new_top ul li .top_info {
    flex: 5;
    /* background-color: orchid; */
}
.new_top ul li .btn {
    position: absolute;
    right: 0;
    width: 46px;
    height: 48px;
    background-color: #c0392b;
    color: #f6f5ec;
    font-size: 12px;
    text-align: center;
    line-height: 48px;
    text-decoration: none;
    // display: none;
    z-index: 99;
    opacity: 0;
    cursor: pointer;
    transition: all .5s;
}
.new_top ul li::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #000;
    opacity: 0;
    transition: all .5s;
}
// 
ul li:hover::after {
    opacity: 0.2;
}
ul li:hover .btn {
    // display: block;
    opacity: 1;
}
ul li .btn:hover {
    color: #fff;
    font-size: 14px;
    background-color: #d54d3f;
}
// 
.num {
    background: url("../../assets/num.png") no-repeat;
    background-position-x: 2px;
}
// 
.top_pic .icon {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 40px;
    height: 40px;
    border-radius: 5px;
    background: url('../../assets/null.jpg') center;
    background-repeat: no-repeat;
    background-size: cover; // 
}
// 
.top_info p {
    display: block;
    height: 48px;
    color: #99A9BF;
    font-size: 14px;
    line-height: 48px;
    overflow: hidden;
}
// 
// .el-icon-star-on {
