<template>
    <div class="category-container">
        <!--  -->
        <div class="category_content">
            <div class="category_bj">
                <img :src="bjPic" alt="" />
            </div>
            <span class="total">{{ total }}</span>
            <h1 class="archive-title">{{ classifyTitle }}</h1>
            <!-- Tab  -->
            <div class="category_class">
                <!--  -->
                <div class="filter-item">
                    <ul class="filter-tag">
                        <div class="g-class-top">
                            <i class="el-icon-menu"></i>
                            <span></span>
                        </div>
                        <li 
                            v-for="item in dts" 
                            :key="item.id"
                            @click="change(item.id, item.icon, item.name)"
                            :class="{ active: item.id == categoryId }">
                            {{ item.name }}
                        </li>
                    </ul>
                </div>
                <!--  -->
                <div class="filter-item">
                    <ul class="filter-tag">
                        <div class="g-class-top">
                            <i class="el-icon-s-grid"></i>
                            <span></span>
                        </div>
                        <li 
                            v-for="item in dts2" 
                            :key="item.id"
                            @click="change2(item.id, item.name)"
                            :class="{ active: item.id == subCategoryID }">
                            {{ item.name }}
                        </li>
                    </ul>
                </div>
                <!--  -->
                <div class="filter-item">
                    <ul class="filter-tag">
                        <div class="g-class-top">
                            <i class="el-icon-s-data"></i>
                            <span></span>
                        </div>
                        <li 
                            v-for="(item, index) in range" 
                            :key="index"
                            @click="change3(index)"
                            :class="{ active: rangeIndex == index }">
                            {{ item }}
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <!--  -->
        <div class="category_list w">
            <el-row :gutter="15">
                <VideoInfo
                    v-for="item in classifyVideo"
                    :key="item.id"
                    :span="6"
                    :id="item.id"
                    :title="item.title"
                    :pic="item.thumb"
                    :longUrl="item.video_url"
                    :shortUrl="item.short_video_url"
                    :view="item.view_num"
                    :like="item.like_num"
                    :tags="item.tags"
                    :time="item.mtime"
                    :category="item.category_name"
                    :topic_info="item.topic_info"
                    :is_like="item.is_like"
                >
                </VideoInfo>
            </el-row>
        </div>
        <!--  ElementUi -->
        <div class="pager-box">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pagenum"
                :page-sizes="[16, 20, 32, 60]"
                :page-size="pagesize"
                :total="total"
                layout="total, sizes, prev, pager, next"
                prev-text=""
                next-text=""
                background
                style="text-align: center"
                :hide-on-single-page="true"
            >
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            dts: [],
            fristDt:{ id: 0, name: "", icon: "http://121.4.184.254/images/1.jpg" },
            // idtab  
            categoryId: 0, // 
            classifyVideo: [],
            classifyTitle: "", // 
            bjPic: "http://121.4.184.254/images/1.jpg",
            // 
            dts2: [{id: null, name: ""}],
            subCategoryID: 0, // 
            order: "", // 012
            // 
            rangeIndex: 0,
            range: ["", "", ""],
            // 
            pagenum: 1, // 
            pagesize: 16, // 
            total: 0, // 
            mid: 0,
        };
    },
    methods: {
        // Tab 
        change(id, pic, title) {
            this.categoryId = id;
            this.classifyTitle = title;
            this.bjPic = pic;
            this.pagenum = 1; // Tab,1
            this.subCategoryID = ""
        },
        change2(id, title) {
            // this.categoryId = id;
            this.subCategoryID = id;
            // this.classifyTitle = title;
            this.pagenum = 1; // Tab,1
        },
        change3(index) {
            this.rangeIndex = index
            // this.pagenum = 1;
            this.getCategoryVideo(this.mid);
        },
        //  axios 
        getCategory () {
            this.$http.video.category().then(({ data }) => {
                this.dts = data;
                this.dts.unshift(this.fristDt) // 
                this.categoryId = this.dts[0].id; //  id  categoryId
            })
        },
        //  axios   
        getCategoryVideo (mid) {
            this.$http.video.list({
                page: this.pagenum, // 
                limit: this.pagesize, // 
                category_id: mid, // id
                order: this.rangeIndex // 
            }).then(({ data }) => {
                this.classifyVideo = data.list;
                this.total = data.total; // 
                if(this.dts2===undefined || this.dts2.length<1) {
                    this.dts2 = [{id: null, name: ""}];
                }
            })
        },
        //--------------------------------------
        // pageSize 
        handleSizeChange(newSize) {
            // console.log(` ${newSize} `);
            this.pagesize = newSize;
            this.getCategoryVideo(this.mid, this.topicID);
        },
        // currentPage 
        handleCurrentChange(newPage) {
            // console.log(`: ${newPage}`);
            this.pagenum = newPage;
            this.getCategoryVideo(this.mid, this.topicID);
        },
    },
    //  categoryId ,
    watch: {
        //  categoryId 
        categoryId: {
            // handler , categoryId , handler 
            handler(newID) {
                this.mid = newID
                this.categoryId = newID;
                this.subCategoryID = newID
            },
            // immediate: true, // 
        },
        subCategoryID: {
            handler(newID) {
                this.mid = newID
                this.subCategoryID = newID;
                this.getCategoryVideo(newID);
                this.dts2 = this.dts[this.categoryId].children;
            },
        },
    },
    mounted() {
        this.getCategory();
        this.getCategoryVideo();
    },
};
</script>

<style lang="less" scoped>
// 
.active {
    color: #f3f3f3 !important;
    background-color: #fb7299 !important;
    border: 1px solid transparent !important;
    // font-size: 30px !important;
    // filter: brightness(100%) !important;
    // box-shadow: 0px 12px 8px -12px #000;
}
.category-container {
    padding-top: 135px;
    .category_content {
        position: relative;
        height: 350px;
        // background-color: green;
        .category_bj {
            position: relative;
            width: 100%;
            height: 100%;
            overflow: hidden;
            // background-color: orange;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                // filter: blur(10px); // 
                // -webkit-filter: blur(10px);
                // 
                animation: move 25s linear 2 alternate;
                animation-iteration-count: infinite; // 
            }
            // 
            @keyframes move {
                0%{
                    transform: scale(1);
                    filter: blur(10px);
                    -webkit-filter: blur(10px);
                }
                100%{
                    transform: scale(1.5);
                    filter: blur(0);
                    -webkit-filter: blur(0);
                }
            }
        }
        .category_bj::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: #000;
            opacity: 0.3;
        }
        .archive-title, .total {
            color: #f3f3f3;
            font-size: 40px;
            text-shadow: rgba(14, 14, 37, 0.87) -2px 2px 10px;
            position: absolute;
            top: 20%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
        .total {
            top: 50%;
            font-size: 100px;
            opacity: .3;
        }
        .category_class {
            position: absolute;
            bottom: -80px;
            left: 50%;
            transform: translate(-50%);
            width: 1200px;
            height: 160px;
            background-color: #34425a;
            border-radius: 10px;
            box-shadow: 0px 12px 8px -12px #000;
            .filter-item {
                height: 26px;
                margin-top: 20px;
                overflow: hidden;
                color: #f3f3f3;
                text-shadow: rgba(14, 14, 37, 0.87) -2px 2px 10px;
                .filter-tag li, .g-class-top {
                    display: inline-block;
                    padding: 1px 10px;
                    border: 1px dashed #a5a5a5;
                    border-radius: 4px;
                    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2);
                    background-color: rgba(0, 0, 0, 0);
                    margin-right: 10px;
                    cursor: pointer;
                }
                .filter-tag .g-class-top {
                    display: inline-block;
                    border: 1px solid transparent;
                    background-color: rgba(0, 0, 0, 0);
                    margin-left: 10px;
                    color: #fff;
                }
                .g-class-top span {
                    margin-left: 5px;
                }
                .filter-tag li:hover {
                    color: #fff;
                    background-color: #fb7299;
                    border: 1px solid transparent;
                }
            }
        }
    }
    .category_list {
        padding-top: 100px;
    }
}
// 
.pager-box /deep/ .el-pagination {
    padding: 20px 0;
    .el-pagination__total {
        padding: 0 14px;
        min-width: 35.5px;
        color: #99a9bf;
        border: 1px solid #34425a;
    }
    .el-pagination__total,
    .btn-prev,
    .btn-next,
    .number,
    .el-input--mini,
    .el-input__inner,
    .el-icon {
        height: 39px;
        line-height: 39px;
        border-radius: 3px !important;
        font-size: 14px;
        font-weight: 700;
        // background: #34425a !important;
        box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2);
    }
    .el-input--mini .el-input__inner {
        font-size: 15px;
        background-color: rgba(0, 0, 0, 0);
        color: #99a9bf;
        border: 1px solid #34425a;
    }
    button span {
        font-size: 16px !important;
        width: 68px;
        height: 39px;
        line-height: 39px;
        font-weight: 700;
    }
    .number {
        width: 39px;
    }
    .number:hover,
    .el-icon:hover {
        // background-color: #fb7299 !important;
        color: #fff !important;
        transition: all 0.5s;
    }
}
// 
.pager-box
    /deep/
    .el-pagination.is-background
    .el-pager
    li:not(.disabled).active {
    background-color: #fb7299 !important;
}
.pager-box /deep/ .el-pagination.is-background .el-pager li {
    margin: 0 5px;
    background-color: #34425a;
    color: #99a9bf;
}
// 
.pager-box /deep/ .el-pagination.is-background .btn-prev {
    background-color: #34425a;
    color: #fff;
}
.pager-box /deep/ .el-pagination.is-background .btn-next {
    background-color: #34425a;
    color: #fff;
}
.pager-box /deep/ .el-pagination.is-background .btn-prev:disabled {
    background-color: #34425a;
    color: #99a9bf;
    opacity: 0.5;
}
.pager-box /deep/ .el-pagination.is-background .btn-next:disabled {
    background-color: #34425a;
    color: #99a9bf;
    opacity: 0.5;
}
</style>