import Vue from 'vue'
import VueRouter from 'vue-router'
// 
import Home from '@/views/Home.vue'
import Meinv from '@/views/Meinv.vue'
import Lib from "@/views/Lib.vue"
import Playe from "@/views/playe.vue"
import Category from "@/views/Category.vue"
// import Search from "@/views/Search.vue"

Vue.use(VueRouter)

const routes = [
  // meta  title
  { path: "/", component: Home, meta: { title: "" } },
  { path: "/meinv", component: Meinv, meta: { title: "" } },
  { path: "/lib/:id", component: Lib, name: "myLib" },
  // params   name 
  { path: "/playe", component: Playe, name: "myPlaye", meta: { title: "" }, props: true },
  { path: "/category", component: Category, name: "myCategory", meta: { title: "" } },
  // { path: "/search", component: Search, name: "mySearch" },
]

const router = new VueRouter({
  routes
})

export default router
