/**
 * 1. axios
 * 
 */
import axios from 'axios'
import store from '../store/index'

/**
 * 
 * @param {Number} status 
 */
const errorHandle = (status, other) => {
    // 
    switch (status) {
        // 401: 
        case 401:
            toLogin()
            break
        // 403 token
        // token
        case 403:
            tip('')
            localStorage.removeItem('token')
            store.commit('loginSuccess', null)
            setTimeout(() => {
                toLogin()
            }, 1000)
            break
        // 404
        case 404:
            tip('')
            break
        case 400:
            tip('')
            break
        default:
        // console.log(other)
    }
}

// axios
const instance = axios.create({ baseURL: 'http://121.4.184.254:1234/api/v1', timeout: 1000 * 60 })
// post
instance.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'

/**
 * interceptors 
 *  axios 
 * 
 * 
 */
instance.interceptors.request.use(
    config => {
        const token = store.state.token
        token && (config.headers.authorization = token)
        if (!config._type) {
            config.data = { ...config.data, timestamp: parseInt(Date.now() / 1000) }
        }
        return config
    },
    error => Promise.error(error))

// 
instance.interceptors.response.use(
    // 
    res => {
        if (res.status === 200) {
            const _authorization = res.headers.authorization
            const { code, msg } = res.data
            if (code === 400) {
                console.log(msg);
            }
            if (_authorization) {
                store.commit('setToken', _authorization)
            }
            // if (code === 500) {
            //     toLogin()
            // }
            return Promise.resolve(res.data)
        } else {
            return Promise.reject(res)
        }
    },
    // 
    error => {
        const { response } = error
        if (response) {
            // 2xx
            errorHandle(response.data.code, response.data.message)
            return Promise.reject(response)
        } else {
            // 
            // eg:statenetwork
            // networkapp.vue
            // 
            if (!window.navigator.onLine) {
                store.commit('changeNetwork', false)
            } else {
                return Promise.reject(error)
            }
        }
    })

export default instance