<template>
    <div class="lib-container w">
        <div class="mod-movie-wrap">
            <!--  -->
            <div class="movie-poster-img" 
                v-if="mnPic"
                :style="{backgroundImage: 'url('+mnPic+')'}">
            </div>
            <div v-else class="movie-poster-img"></div>
            <!--  -->
            <div class="movie-mask"></div>
            <!--  -->
            <div class="mod-movie">
                <ul>
                    <li><h1>{{ mnName }}</h1></li>
                    <li>{{ mnSummary }}</li>
                    <li>{{ mnWorks }}</li>
                </ul>
            </div>
        </div>
        <!--  -->
        <!--  -->
        <div class="category_list w">
            <el-row :gutter="15">
                <VideoInfo
                    v-for="item in mnMovieList"
                    :key="item.id"
                    :span="6"
                    :id="item.id"
                    :title="item.title"
                    :pic="item.thumb"
                    :longUrl="item.video_url"
                    :shortUrl="item.short_video_url"
                    :view="item.view_num"
                    :like="item.like_num"
                    :tags="item.tags"
                    :time="item.mtime"
                    :topic_info="item.topic_info"
                    :is_like="item.is_like"
                >
                </VideoInfo>
            </el-row>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            // 
            mnName: "", // 
            mnPic: "", // 
            mnSummary: "", // 
            mnWorks: "", // 
            mid: "",
            // 
            mnMovieList: [],
        }
    },
    methods: {
        // ()
        getPathInfo() {
            // console.log(this.$route);
            // query 
            this.mnName = this.$route.query.title
            // params 
            // this.mnName = this.$route.params.title

            // local  params 
            let dataObj = JSON.parse(localStorage.getItem("mnInfo"));
            this.mid = dataObj.id // id
            this.mnPic = dataObj.pic // 
            this.mnSummary = dataObj.summary // 
            this.mnWorks = dataObj.works // 

            // 
            this.$http.video.videoStar({
                category_id: this.mid
            }).then(({data})=> {
                this.mnMovieList = data.list
                console.log(data.list);
            })
        }
    },
    mounted() {
        this.getPathInfo()
        //   title 
        window.document.title = this.$route.query.title
    }
}
</script>

<style lang="less" scoped>
.lib-container {
    padding-top: 145px;
}
.mod-movie-wrap {
    position: relative;
    width: 1200px;
    height: 420px;
    border-radius: 15px;
    margin-bottom: 10px;
    background-color: #222;
    z-index: 12;
    overflow: hidden;
    .movie-poster-img {
        position: absolute;
        top: 0;
        // right: 50%;
        right: 0;
        // width: 1200px;
        width: 900px;
        height: 420px;
        // margin-right: -705px;
        overflow: hidden;
        background: url("../assets/null.jpg") no-repeat center;
        background-size: cover;
    }
    .movie-mask {
        width: 100%;
        height: 420px;
        overflow: hidden;
        margin: 0 auto;
        position: absolute;
        top: 0;
        left: 0;
        background: url("../assets/TB.png") no-repeat center 0 transparent;
    }
    .mod-movie {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        color: #fff;
        width: 500px;
        height: 200px;
        padding: 30px;
        margin: 0 auto;
        overflow: hidden;
        ul {
            display: flex;
            flex-direction: column;
            height: 100%;
            li {
                flex: 1;
            }
        }
    }
}
</style>