<template>
    <div class="mnlib-container">
        <!--  -->
        <!--  ElementUI  Layout  -->
        <!-- gutterspan24,8 -->
        <el-row :gutter="15">
            <el-col :span="4" v-for="item in mnData" :key="item.id">
                <div class="film_show">
                    <a @click="toPath(item.id, item.name, item.icon, item.summary, item.works)" class="a_cover">
                        <img :src="item.icon" alt="">
                    </a>
                    <span class="mn_name">{{item.name}}</span>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    //  MnListSearch  
    props: ["mnData"],
    methods: {
        // ,,/
        toPath(id, name, pic, summary, works) {
            const { href } = this.$router.resolve({
                path: "/lib/" + id,
                query: { title: name},
            });
            // local
            localStorage.setItem('mnInfo', JSON.stringify({
                id: id, pic: pic, summary: summary, works: works
            }))
            window.open(href, '_blank');
            // this.$router.push({
            //     path: "/lib/" + id,
            //     // query get ,
            //     query: { title: name},
            //     // params  post,
            //     // params: { pic: pic, summary: summary, works: works},
            // })
            // // local
            // localStorage.setItem('mnInfo', JSON.stringify({
            //     pic: pic, summary: summary, works: works, movies: movies
            // }))
        },
    },
}
</script>

<style lang="less" scoped>
.film_show {
    position: relative;
    // 
    height:0;
    padding-bottom:100%;
    border-radius: 4px;
    background-color: #34425a;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);
    margin-bottom: 15px;
    .a_cover {
        position: relative;
        width: 70%;
        // 
        height:0;
        padding-bottom:70%;
        border-radius: 50%;
        position: absolute;
        top: 8%;
        left: 50%;
        transform: translate(-50%);
        box-shadow: 0px 12px 8px -12px #000;
        cursor: pointer;
        overflow: hidden;
        img {
            position: absolute;
            width: 100%;
            height: 100%;
            // 
            object-fit: cover;
            transition: all .5s;
            background: url('../../assets/null.jpg') no-repeat center;
            background-size: cover;
        }
        img:hover {
            filter: brightness(75%); // 
            transform: scale(1.2); // 
        }
    }
    .mn_name {
        width: 70%;
        height: 24px;
        position: absolute;
        bottom: 5%;
        left: 50%;
        transform: translate(-50%);
        color: #f3f3f3;
        line-height: 24px;
        text-align: center;
        overflow: hidden;
        text-shadow: rgba(14, 14, 37, 0.87) -2px 2px 10px;
    }
}
</style>