<template>
    <!--  -->
    <div class="videoInfo">
        <!--  ElementUI  Layout  -->
        <!-- span24,8 -->
        <el-col :span="span">
            <div class="grid-content">
                <!--  -->
                <a
                    class="list-img"
                    @click="toPath(id, title, longUrl)"
                    @mouseover.self="change1"
                    @mouseout.self="change2"
                >
                    <img :src="pic" alt="" />
                </a>
                <!--  category (),label -->
                <span :class="{ label: category }">{{ category }}</span>
                <!--  -->
                <i id="btn_like" 
                    v-if="$store.state.user"
                    :class="isLike === 0 ? noLike : yesLike" 
                    @click="_like(id)">
                </i>
                <!--  -->
                <div class="list-featured-tag">
                    <!--  -->
                    <div class="outline-tag">
                        <span
                            class="badge"
                            v-for="tag in tagsArr"
                            :key="tag"
                            :style="{
                                color: TagColor,
                                border: '1px solid' + TagColor,
                            }"
                        >
                            {{ tag ? tag : "" }}
                        </span>
                    </div>
                    <!--  -->
                    <p class="list-title">{{ title }}</p>
                    <!--  -->
                    <div class="list-footer">
                        <div class="time">
                            <!-- ElementUI -->
                            <i class="el-icon-time">&nbsp;{{ time }}</i>
                        </div>
                        <div class="star">
                            <!-- ElementUI -->
                            <i class="el-icon-view">&nbsp;{{ view }}</i>
                            <i class="el-icon-star-off">&nbsp;{{ likeNum }}</i>
                        </div>
                    </div>
                </div>
            </div>
        </el-col>
    </div>
</template>

<script>
export default {
    // 
    props: {
        // 
        span: {
            default:"",
            type: [Number, String]
        },
        // id
        id: {
            default: "",
            type: Number,
        },
        // 
        title: {
            default: "", // 
            type: String, // 
        },
        // 
        pic: {
            default: "",
            type: String,
        },
        // 
        longUrl: {
            default: 0,
            type: String, // 
        },
        // 
        shortUrl: {
            default: "",
            type: String,
        },
        // 
        view: {
            default: "",
            type: Number,
        },
        // 
        like: {
            default: "",
            type: Number,
        },
        // 
        tags: {
            default: "",
            type: String,
        },
        // 
        time: {
            default: "",
            type: String,
        },
        // 
        category: {
            default: "",
            type: String,
        },
        // 
        topic_info: {
            default: {},
            type: Object,
        },
        // 
        is_like: {
            default: 0, // 0:,1:
            type: Number
        }
    },
    data() {
        return {
            // 
            TagColor: "#F66",
            //  props  
            tagsArr: this.tags.split(","),
            // 
            yesLike: "el-icon-remove-outline",
            noLike: "el-icon-circle-plus",
            isLike: this.is_like, // 
            likeNum: this.like, // 
        };
    },
    methods: {
        // ,
        change1(e) {
            //   change 
            e.target.parentNode.className = "grid-content change";
        },
        // ,
        change2(e) {
            //   change 
            e.target.parentNode.className = "grid-content";
        },
        // ,,
        toPath(id, tit, url) {
            const { href } = this.$router.resolve({
                name: "myPlaye",
                query: { id: id },
            });
            // local
            localStorage.setItem(
                "rowData",
                JSON.stringify({
                    src: url,
                    title: tit,
                })
            );
            window.open(href, "_blank");
            // this.$router.push({
            //     // params   name 
            //     name: "myPlaye",
            //     //  query  get ,
            //     query: { id: id},
            //     //   params  post,
            //     params: { src: url, title: tit }
            // })
        },
        // 
        _like(id) {
            if(this.isLike === 0) {
                this.$http.user.like_on({ vid: id }).then(({ code }) => {
                    if (code === 200) {
                        this.isLike = 1
                        this.likeNum += 1 // 
                    }
                })
            } else {
                this.$http.user.like_off({ vid: id }).then(({ code }) => {
                    if (code === 200) {
                        this.isLike = 0
                        this.likeNum -= 1 // 
                    }
                })
            }
        }
    },
};
</script>

<style lang="less" scoped>
//  ElementUI  Layout 
.el-row {
    margin-top: 20px;
    &:last-child {
        margin-bottom: 0;
    }
}
.el-col {
    margin-bottom: 15px;
    .grid-content {
        position: relative;
        height: 310px;
        border-radius: 10px;
        // background-color: #fff;
        background-color: #34425a; // 
        box-shadow: 0 8px 6px rgba(0, 0, 0, 0.05);
        overflow: hidden;
        // 
        .list-img {
            position: relative;
            // display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 195px;
            overflow: hidden;
            cursor: pointer;
            img {
                width: 100%;
                height: 100%;
                // 
                object-fit: cover;
                transition: all 0.5s ease-out;
            }
        }
        // category,
        .label {
            position: absolute;
            top: 12px;
            left: 12px;
            padding: 3px 10px;
            border-radius: 2px;
            color: #fff;
            font-size: 12px;
            font-family: OPPOSans2;
            font-weight: 400;
            background: #f2994a;
            background: -o-linear-gradient(left, #ffc022, #f2994a);
            background: -webkit-gradient(
                linear,
                left top,
                right top,
                from(#ffc022),
                to(#f2994a)
            );
            background: linear-gradient(to right, #ffc022, #f2994a);
            z-index: 1;
        }
        // 
        #btn_like {
            position: absolute;
            top: 12px;
            right: 12px;
            color: rgba(255, 255, 255, 0.8);
            font-size: 30px;
            z-index: 1;
            cursor: pointer;
            background: rgba(0, 0, 0, 0.1);
            border-radius: 50%;
        }
        // 
        .list-img::after {
            content: "";
            position: absolute;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: #000;
            // 
            opacity: 0.25;
            transition: all 0.5s;
        }
        // 
        .list-img:hover::after {
            // 
            opacity: 0;
        }
        .list-img:hover img {
            // 1.2
            transform: scale(1.2);
        }
        // 
        .list-featured-tag {
            width: calc(100% - 24px);
            height: 91px;
            padding: 12px;
            position: absolute;
            bottom: 0;
            // font-family:OPPOSans2;
            .outline-tag {
                width: 100%;
                height: 25px;
                overflow: hidden;
                .badge {
                    display: inline-block;
                    height: 14px;
                    font-size: 12px;
                    // 
                    color: #f66;
                    border: 1px solid #f66;
                    padding: 4px 10px;
                    margin-right: 5px;
                    margin-top: 1px;
                    border-radius: 3px;
                }
            }
            // 
            .list-title {
                display: block;
                height: 24px;
                line-height: 24px;
                // color: #333;
                color: rgb(153, 169, 191); // 
                font-size: 14px;
                margin-top: 10px;
                overflow: hidden;
                text-overflow: ellipsis; //
                white-space: nowrap; //
            }
            // 
            .list-footer {
                height: 24px;
                // color: #333;
                color: rgb(153, 169, 191); // 
                margin-top: 15px;
                font-size: 12px;
                line-height: 24px;
                .time {
                    float: left;
                    width: 120px;
                    overflow: hidden;
                }
                .star {
                    float: right;
                    i {
                        padding-left: 10px;
                    }
                }
            }
        }
    }
    // ,
    .change {
        background-color: rgba(0, 0, 0, 0.05);
        box-shadow: 0px 12px 8px -12px #000;
        transition: all 0.5s;
    }
}
</style>